.collection {
  margin-top: 120px;

  @media (max-width: 900px) {
    margin-top: 70px;
  }
}

.collection__title {
  font-size: 45px;
  line-height: 50px;

  @media (max-width: $tablet-width) {
    font-size: 30px;
  }
}

.collection__nav-item {
  padding-right: 40px;

  &:hover {
    color: $hover-color;
  }
}

.collection__top {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-bottom: 10px;
  margin-bottom: 24px;
  border-bottom: 1px solid #000000;
}

.collection__nav {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 40px;

  @media (max-width: 900px) {
    display: none;
  }
}

.collection__arr-img {
  padding-right: 5px;
  transform: rotate(180deg);
}

.collection__txt {
  display: flex;
  flex-direction: row;
  align-items: baseline;
}

.collection__arr {
  display: flex;
  flex-direction: row;
}

.collection__images {
  @media (max-width: 900px) {
    display: block;
    width: 100%;
    position: relative;
  }

  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-column-gap: 20px;
}

.collection__photo {
  
  width: 100%;
  margin-right: 20px;
  display: block;

  @media (max-width: 900px) {
    object-fit: contain;
    margin: 0;
  }
}

.collection__images-shell {
  position: relative;
  width: 100%;
}

.collection__btn-plus {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.collection__btn-plus-black  {
  background-color: #000000;
  width: 25px;
  height: 25px;
  border: 2px solid $white-color;
  border-radius: 50%;
  position: relative;
  cursor: pointer;

  &::before {
    content: '';
    width: 13px;
    height: 1px;
    background-color: $white-color;
    position: absolute;
    transform: translate(-50%, -50%);
  }

  &::after {
    content: '';
    width: 1px;
    height: 13px;
    background-color: $white-color;
    position: absolute;
    transform: translate(-50%, -50%);
  }
}

.collection__btn-plus-black:nth-child(1) {
  left: 10%;
  bottom: 26%;
  position: absolute;

  @media (max-width: 980px) {
    left: 14%;
    top: 26%;
  }
}

.collection__btn-plus-black:nth-child(2) {
  left: 45%;
  top: 37%;
  position: absolute;

  @media (max-width: 980px) {
    left: 65%;
    top: 43%;
  }
}

.collection__btn-plus-black:nth-child(3) {
  right: 18%;
  bottom: 20%;
  position: absolute;

  @media (max-width: 980px) {
    left: 50%;
    bottom: 17%;
  }
}



.black-active {
  background-color: $main-border-color;
  width: 45px;
  height: 45px;
  border: none;
  border-radius: 50%;
  position: relative;

  &::before {
    content: '';
    width: 13px;
    height: 1px;
    background-color: $white-color;
    position: absolute;
    transform: translate(-50%, -50%);
  }

  &::after {
    content: '';
    width: 1px;
    height: 13px;
    background-color: $white-color;
    position: absolute;
    transform: translate(-50%, -50%);
  }
}

.collection__btn-plus-black:nth-child(4) {
    left: 29%;
    bottom: 36%;
    position: absolute;
  
    @media (max-width: 980px) {
      left: 10%;
      bottom: 36%;
    }
}

.coll {
  grid-column-start: 1;
  grid-column-end: 7;

  @media (max-width: 900px) {
    position: relative;
  }
}

.collection__btn {
  position: relative;
  cursor: pointer;
  background-color: transparent;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  border: none;
  // border: 1px solid #000000;

  // &::after {
  //   content: '';
  //   position: absolute;
  //   background-image: url('../img/icons/icon-arr-button.svg');
  //   width: 8px;
  //   height: 16px;
  //   transform: translate(-50%,-50%);
  // }

  &--next {
    transform: rotate(180deg);
    margin-left: 5px;
  }

  .collection__btn-icon {
    position: absolute;
    top: 0;
    left: 0;
  }

  &:hover {
    background-color: $main-border-color;
    color: $white-color;
    border-color: $white-color;
  }
}